<template>
	<div>
		<!--<div class="container">-->
		<div class="col-sm-9 col-sm-offset-3 col-md-10 col-md-offset-2 main">
			<div style="margin-right: 0px;" class="row clearfix">
				<div class="page-header">
					<h1>
					Upload My Video <small></small>
				</h1>
				</div>
				<div class="col-md-8 column">
					
						<div class="form-group">
							<label for="exampleInputEmail1"></label>
							<input type="text" name="title" v-model="title" class="form-control" placeholder="" id="exampleInputEmail1" />
						</div>

						<div class="form-group">
							<label for="exampleInputEmail1"></label>
							<select name="categoryid" class="form-control" v-model="categoryid">
								<option value="">----</option>
								<option :value="category.id" v-for="category in categorys"  >{{category.name}}</option>
							</select>
						</div>
						<div class="form-group">
							<label for="name" >:</label>
							<textarea class="form-control" v-model="vediodetail" placeholder="" name="vediodetail" rows="3"></textarea>
						</div>
						<div class="form-group">
							<label for="lastname">:      <span style="color: red;">()</span></label>

							<upload @func="synImg"></upload>

						</div>
						<button type="button" v-on:click="sub" class="btn btn-default"></button>
				
				</div>
			</div>
		</div>

		<!--</div>-->
	</div>
</template>

<script>
	import upload from '@/components/upload'
	export default {
		name: 'my_upload',
		data() {
			return{
				title:'',
				categoryid:'',
				categorys:[],
				vediodetail:'',
				imgList: []
			}
			
		},
		components: {
			upload //
		},
		created(){
				this.loadCategory();
		},
		methods: {
			synImg: function(data) {
				this.imgList = data; //imgLis
			},
			sub: function() {
				var formdata = new FormData();
				for(var i = 0; i < this.imgList.length; i++) { //
					formdata.append("file", this.imgList[i].file);
				}
					formdata.append("title",this.title);
					formdata.append("categoryid",this.categoryid);
					formdata.append("vediodetail",this.vediodetail);
				var header = { //
					headers: {
						"Content-type": "multipart/form-data"
					}
				};
				this.$http.post("http://47.98.178.173/Video/uploadVideo", formdata, header).then(
					function(result) {
						alert("");
						
						this.$router.go(0); //

					},
					function(error) {
						
						alert("");
					})
			},
			//
			loadCategory:function(){
				this.$http.get("http://47.98.178.173/Video/selectAllCategory").then(
					function(result){
						this.categorys=result.body;
				},function(error){
					alert("")
				})
			}
		}
	}
</script>

<style scoped>
	.clearfix {
		margin-left: 50px;
		font-size: 13px;
	}
</style>


