<template>
	<div>
		<div class="col-sm-9 col-sm-offset-3 col-md-10 col-md-offset-2 main">
			<div class="col-md-8">
				<!---->
				<div>
					<div class="Dplayer_box">
						<h5>{{video.title}}</h5>
						<div class="player_av">
							<div ref="dplayer" id="dplayer"></div>
						</div>
					</div>
				</div>
				<!---->
				<!---->
				<div id="buttons">
					<button type="button" v-if="goodFlag==false" class="btn btn-default buttons" @click="thumbsup">
						<span class="glyphicon glyphicon-thumbs-up">{{good}}</span>
					</button>
					<button type="button" v-if="goodFlag==true" class="btn btn-info buttons" @click="thumbsup">
						<span class="glyphicon glyphicon-thumbs-up">{{good}}</span>
					</button>
					<button type="button" v-if="badFlag==false" class="btn btn-default buttons" @click="thumbsdown">
						<span class="glyphicon glyphicon-thumbs-down">{{bad}}</span>
					</button>
					<button type="button" v-if="badFlag==true" class="btn btn-danger buttons" @click="thumbsdown">
						<span class="glyphicon glyphicon-thumbs-down">{{bad}}</span>
					</button>
					<button type="button" class="btn btn-default buttons" @click="insertcollection">
						<span v-if="this.collection==''" class="glyphicon glyphicon-star-empty">{{collection}}</span>	
						<span v-if="this.collection==''" class="glyphicon glyphicon-star">{{collection}}</span>		
					</button>
					<a type="button" class="btn btn-default buttons" :href="'http://47.98.178.173/downloadVideo?fileName='+this.video.videourl">
						<span class="glyphicon glyphicon-circle-arrow-down"></span>
					</a>
					<button style="padding: 0px;border:0px" type="button" class="btn btn-default buttons"><share :config="config"></share></button>

				</div>
				<!---->
				<!---->
				<div id="videoDetail">
					<div id="detail">
						<p class="details"></p>
						<div class="details" id="avgmark"></div>
						<p>{{video.vediodetail}}</p>
						<div>
							<p class="details"></p>
							<div class="details" id="mark"></div>
						</div>
					</div>

				</div>
				<!---->
				<!---->
				<div class="all-comments">
					<div class="all-comments-info">
						<a> (*)</a>
						<div class="box">
							<form>
								<textarea placeholder="" v-model="content" required=" "></textarea>
								<input type="button" style="display: block;margin: 0px 0px 0px 250px;" class="btn btn-default" @click="sendComment" value="SEND" />
							</form>
						</div>
						<div class="all-comments-buttons">
							<ul>
								<li>
									<a @click="getNewComment" class="top newest"></a>
								</li>
								<li>
									<a @click="getMyComment" class="top my-comment"></a>
								</li>
							</ul>
						</div>
					</div>
					<div class="media-grids">

						<div class="media" v-for="userComment in userComments">
							<h5>{{userComment.name}}</h5>
							<div class="media-left">
								<a><img :src="userComment.photourl" />
								</a>
							</div>
							<div class="media-body">
								<p>{{userComment.content}}</p>
							</div>
						</div>
					</div>
				</div>
				<!---->
			</div>

			<!---->
			<div class="col-md-4 single-right">
				<h3></h3>
				<div id="author">
					<div id="author-left">
						<a style="cursor: pointer;" @click="selectAuthor(author.id)"><img :src="author.photourl" /></a>
					</div>
					<div id="author-right">
						<h4 style="line-height: 1.5em;">{{author.name}}</h4>
						<p style="line-height: 2.3em;" class="signature">{{author.signature}}</p>
						<button class="btn btn-default" @click="insertfollow">
						<span class="glyphicon glyphicon-plus"></span>{{follow}}
					</button>
						<button style="margin-left: 20px;" class="btn btn-default" @click="privateLetter(author.id)"> 
						<span class="glyphicon glyphicon-envelope"></span>
					</button>

					</div>
				</div>
				<h3></h3>
				<div class="single-grid-right">
					<div v-for="recommendVideo in recommendVideos" class="single-right-grids">
						<div class="col-md-4 single-right-grid-left">
							<p style="cursor: pointer;" class="author-video" @click="toVideo(recommendVideo.vid)"><img :src="recommendVideo.photourl" /></p>
						</div>
						<div class="col-md-8 single-right-grid-right">
							<a style="cursor: pointer;" @click="toVideo(recommendVideo.vid)" class="title"> {{recommendVideo.title}}</a>
							<p class="author">
								<a style="cursor: pointer;"  class="author">{{recommendVideo.name}}</a>
							</p>
							<p class="views">{{recommendVideo.showcount}}</p>
						</div>
						<div class="clearfix"> </div>
					</div>
				</div>
			</div>
			<!---->
		</div>

	</div>
</template>

<script>
	//Vue.http.options.withCredentials = true;//cookie
	export default {
		name: 'videoShow',
		data() {
			return {
				vid: 1, //id
				good: 0, //
				bad: 0, //
				goodFlag:false,//
				badFlag:false,//
				rateValue: 3, //
				markValue: 0, //
				collection: '',
				follow: '',
				content: '', //
				url: '',
				pic: '',
				msg: '',
				userComments: [],
				author: {},
				video: {},
				recommendVideos: [],
				config: {
					sites: ['qq', 'weibo', 'wechat', 'douban'], // 
					disabled: ['google', 'facebook', 'twitter'] // 
				}
			}
		},
		methods: {
			loadVideo: function() {
				var url = this.url;
				//alert(url);
				var danmakuId = md5(url);
				const dp = new DPlayer({
					container: this.$refs.dplayer,
					//
					autoplay: false,
					// 
					theme: '#FADFA3',
					// 
					loop: false,
					lang: 'zh-cn',
					// 
					screenshot: false,
					// 
					hotkey: true,
					// : 'none', 'metadata', 'auto'
					preload: 'auto',
					//  logo CSS 
					//logo: 'logo.png',
					// 
					volume: 0.7,
					// 
					mutex: true,
					video: {
						//
						url: url,
						//
						pic: this.pic,
						// 
						//thumbnails: 'demo.jpg',
						//
						type: 'auto'
					},
					//
					danmaku: {
						// id
						id: danmakuId,
						// 
						api: 'http://apidp.purvavideha.cn/',
						//  token
						//token: 'tokendemo',
						// 
						//maximum: 1000,
						// 
						//addition: ['https://api.prprpr.me/dplayer/v3/bilibili?aid=4157142'],
						// 
						user: 'moff',
						// : '10px' '10%'
						bottom: '15%',
						// 
						unlimited: true
					},
					// 
					highlight: [{
							time: 60,
							text: '1'
						},
						{
							time: 120,
							text: ' 2 '
						},
						{
							time: 180,
							text: ' 3'
						}
					]
				});
			},
			//
			alerdat_back: function(text) {
				$(".alert_back").html(text).show();
				setTimeout(function() {
					$(".alert_back").fadeOut();
				}, 1200)
			},
			//
			formatTime(seconds) {
				return [
						//            parseInt(seconds / 60 / 60),
						parseInt(seconds / 60 % 60),
						parseInt(seconds % 60)
					]
					.join(":")
					.replace(/\b(\d)\b/g, "0$1");
			},
			//
			sendComment() {
				this.$http.get("http://47.98.178.173/sendcomment", {
					params: {
						vid: this.vid,
						content: this.content
					}
				}).then(
					function(result) {
						this.msg = result.bodyText;
						alert(this.msg);
						this.content = '';
						this.getAllComments();
					},
					function(res) {
						console.log('');
					}
				);
			},
			getAllComments() {
				this.$http.get("http://47.98.178.173/allcomment", {
					params: {
						vid: this.vid
					}
				}).then(
					function(result) {
						this.userComments = result.body;
					},
					function(res) {
						alert('');
					}
				);
			},
			getNewComment() {
				this.$http.get("http://47.98.178.173/getNewComment", {
					params: {
						vid: this.vid
					}
				}).then(
					function(result) {
						this.userComments = result.body;
					},
					function(error) {
						console.log(error);
					});
			},
			getMyComment() {
				this.$http.get("http://47.98.178.173/getMyComment", {
					params: {
						vid: this.vid
					}
				}).then(
					function(result) {
						this.userComments = result.body;
					},
					function(error) {
						console.log(error);
					});
			},
			loadResource() {
				this.vid=this.$route.query.vid;
				this.$http.post("http://47.98.178.173/loadVideo", {
					id: this.vid
				}).then(
					function(result) {
						this.video = result.body;
						this.url = result.body.videourl;
						this.pic = result.body.photourl;
						this.loadVideo();
						this.loadAuthor();
						this.loadGood();
						this.loadBad();
						this.loadMyGood();
						this.loadMyBad();
						this.recommend();
						this.loadFollowAndCollection();
					},
					function(error) {
						console.log(error);
					});
			},
			loadAuthor() {
				this.$http.post("http://47.98.178.173/loadAuthor", {
					id: this.video.userid
				}).then(
					function(result) {
						this.author = result.body;
					},
					function(error) {
						console.log(error);
					});
			},
			loadFollowAndCollection() {
				this.$http.post("http://47.98.178.173/loadFollowAndCollection", {
					vid: this.vid
				}).then(
					function(result) {
						if(result.bodyText == '') {
							this.follow = '';
							this.collection = '';
						} else if(result.bodyText == '') {
							this.follow = '';
						} else if(result.bodyText == '') {
							this.collection = '';
						} else {}
					},
					function(error) {
						console.log(error);
					});
			},
			insertfollow() {
				if(this.follow == '') {
					this.$http.delete("http://47.98.178.173/follow", {
						params: {
							toUid: this.video.userid
						}

					}).then(
						function(result) {
							alert(result.bodyText);
							this.follow = '';
						},
						function(error) {
							console.log(error);
						});
				} else {
					this.$http.get("http://47.98.178.173/follow", {
						params: {
							toUid: this.video.userid
						}
					}).then(
						function(result) {
							alert(result.bodyText);
							this.follow = '';
						},
						function(error) {
							console.log(error);
						});
				}
			},
			insertcollection() {
				if(this.collection == '') {
					this.$http.delete("http://47.98.178.173/collection", {
						params: {
							vid: this.video.id
						}
					}).then(
						function(result) {
							alert(result.bodyText);
							this.collection = '';
						},
						function(error) {
							console.log(error);
						});
				} else {
					this.$http.post("http://47.98.178.173/collection", {
						vid: this.video.id
					}).then(
						function(result) {
							alert(result.bodyText);
							this.collection = '';
						},
						function(error) {
							console.log(error);
						});
				}
			},
			recommend() {
				this.$http.post("http://47.98.178.173/recommend", {
					cid: this.video.categoryid
				}).then(
					function(result) {
						this.recommendVideos = result.body;
					},
					function(error) {
						console.log(error);
					});
			},
			thumbsup() {
				this.$http.post("http://47.98.178.173/thumbsup", {
					vid: this.video.id
				}).then(
					function(result) {
						this.good = result.body;
						this.loadGood();
						this.loadBad();
						this.loadMyGood();
						this.loadMyBad();
					},
					function(error) {
						console.log(error);
					});
			},
			thumbsdown() {
				this.$http.post("http://47.98.178.173/thumbsdown", {
					vid: this.video.id
				}).then(
					function(result) {
						this.bad = result.body;
						this.loadGood();
						this.loadBad();
						this.loadMyGood();
						this.loadMyBad();
					},
					function(error) {
						console.log(error);
					});
			},
			loadGood() {
				this.$http.post("http://47.98.178.173/loadGood", {
					vid: this.video.id
				}).then(
					function(result) {
						this.good = result.body;
					},
					function(error) {
						console.log(error);
					});
			},
			loadBad() {
				this.$http.post("http://47.98.178.173/loadBad", {
					vid: this.video.id
				}).then(
					function(result) {
						this.bad = result.body;
					},
					function(error) {
						console.log(error);
					});
			},
			loadRate() {
				this.$http.get("http://47.98.178.173/loadRate", {
					params: {
						vid: this.vid
					}
				}).then(
					function(result) {
						if(result.body != null || result.body != '') {
							this.rateValue = result.body;
						}else{
							this.rateValue =0;
						}
					},
					function(error) {
						
						this.rateValue =0;
					});
				this.$http.get("http://47.98.178.173/loadMark",{
					params:{
						vid:this.vid
					}
				}).then(
					function(result) {
						if(result.body != null || result.body != '') {
							this.markValue = result.body;
						}else{
							this.markValue =0;
						}
						this.loadMark();
					},
					function(error) {
						this.markValue =0;
						this.loadMark();
					});
			},
			loadMark() {
				var _this =this;
				//
				layui.use('rate', function() {
					var rate = layui.rate;
					//
					var ins1 = rate.render({
						elem: '#avgmark', //
						value: _this.rateValue,
						half: true,
						readonly: true,
						text: true,
						setText: function(value) {
							this.span.text(value + "");
						}
					});
				});
				//
				layui.use('rate', function() {
					var rate = layui.rate;
					//
					var ins1 = rate.render({
						elem: '#mark', //
						value: _this.markValue,
						half: true,
						text: true,
						setText: function(value) {
							this.span.text(value + "");
						},
						choose: function(value) {
							_this.$http.get("http://47.98.178.173/makeMark", {
								params: {
									vcode: value,
									vid:_this.vid
								}
							}).then(
								function(result) {
									if(result.bodyText==''){
										_this.markValue=value;
									}else{
										alert(result.bodyText);
									}
									_this.loadRate();
								},
								function(error) {
									
								});
						}
					});
				});
			},
			privateLetter(id){
				var _this=this;
				layer.prompt({
				  formType: 2,
				  value: 'up',
				  title: '',
				  area: ['350px', '120px'] //
				}, function(value, index, elem){
				  _this.$http.get("http://47.98.178.173/privateLetter", {
								params: {
									content:value,
									'toUid':id
								}
							}).then(
								function(result) {
									alert(result.bodyText);
								},
								function(error) {
									console.log(error);
								});
				  layer.close(index);
				});
			},
			toVideo(vid){
				this.$router.push({
	  				path:'/videoShow',
	  				query:{
	  					vid:vid
	  				}
	  			});
	  			 this.$router.go(0);   
			},
			selectAuthor:function(aid){//
				this.$router.push({
					path:"/solo_message",
					query:{
						"aid":aid
					}
				})
			},
			loadMyGood(){
				this.$http.post("http://47.98.178.173/loadMyGood", {
					vid: this.video.id
				}).then(
					function(result) {
						if(result.bodyText==''){
							this.goodFlag=true;
						}else{
							this.goodFlag=false;
						}
						
					},
					function(error) {
						this.goodFlag=false;//
					});
			},
			loadMyBad(){
				this.$http.post("http://47.98.178.173/loadMyBad", {
					vid: this.video.id
				}).then(
					function(result) {
						if(result.bodyText==''){
							this.badFlag=true;
						}else{
							this.badFlag=false;
						}
					},
					function(error) {
						this.badFlag=false;
					});
			}
		},
		created() {
			this.loadResource();
			this.getAllComments();
			this.loadRate();
		},
		mounted() {

		}
	}
</script>

<style scoped>
	.Dplayer_box {
		overflow: hidden;
		width: 680px;
		height: 456px;
		/*margin: 50px 50px 20px 50px;*/
		display: block;
		border-top: 1px solid #e5e9ef;
		border-bottom: 1px solid #e5e9ef;
		background-color: #f6f9fa
	}
	
	#buttons {
		margin: 10px 10px;
	}
	
	.buttons {
		margin-left: 45px;
	}
	
	#dplayer {
		background-size: 100% 100%;
		margin-top: 30px
	}
	
	.player_av {
		width: 680px;
		height: 100%;
		float: left;
		background: #000
	}
	
	#author,
	#author-right {
		overflow: hidden;
	}
	
	#author-left a {
		width: 65px;
		height: 65px;
		display: inline-block;
		border-radius: 50%;
		-webkit-border-radius: 50%;
	}
	
	#author-left a img {
		width: 65px;
		height: 65px;
		display: inline-block;
		border-radius: 50%;
		-webkit-border-radius: 50%;
	}
	
	#author-left {
		float: left;
		margin: 10px;
	}
	
	#author-right {
		float: left;
		margin: 10px;
	}
	
	.author-video img {
		width: 106px;
		height: 80px;
	}
	
	.details {
		display: inline-block;
	}
	.signature{
		width: 210px;
		display: -webkit-box;
	    -webkit-box-orient: vertical;
	    -webkit-box-pack: center;
	    -webkit-box-align: center;
	    -webkit-line-clamp:1;
	    overflow: hidden;

	}
</style>


